import React from 'react';
import $ from 'jquery';
import 'bootstrap/dist/js/bootstrap.js';

class List extends React.Component{
    constructor(props){
        super(props);
        this.state={
        "List":["orange","apple", "strawberry"],
        };
        this.addItem = this.addItem.bind(this);
        this.Open_model = this.Open_model.bind(this);

    }
 Open_model(){
        $("#myModal").modal();
    }
  addItem(){
        console.log("y")
        this.state.List.push($("#inp").val());
        this.setState(this.state.List);
      
   
    
    }
     render(){
        return(
            <div  className="justify-content-center">
                <div  className="modal fade" id="myModal">
        <div  className="modal-dialog">
        <div  className="modal-content">
        
        
            <div  className="modal-header">
            <h4  className="modal-title">Add items</h4>
            <button type="button"  className="close" data-dismiss="modal">Ã—</button>
            </div>
        
    
            <div  className="modal-body">
                <div className="form-group">
                    <label for="usr">add_items</label>
                    <input type="text" className="form-control" id="inp"/>

                </div>
                <button className="btn btn-primary" onClick={this.addItem}>add</button>
            </div>
    
            <div  className="modal-footer">
            <button type="button"  className="btn btn-danger" data-dismiss="modal">Close</button>
            </div>
            
        </div>
        </div>
    </div>
        
        <div  className="card  justify-content-center mx-auto w-50 shadow  mb-4 bg-white">
                    <div  className="card-header bg-primary">
                        <h4  className="card-title">Awesome Todo list</h4>
                    </div>
                        <div  className="card-body">
                        
                            
                            <ul  class="list-group">
                                { this.state.List.map(item => <List_items name={item} />)}
                                
                                
                            </ul>
                            <button onClick={this.Open_model}>add items</button>
                        </div>
                    </div>
                
                
        
    </div>

        )
    }
}
class List_items extends React.Component{
    constructor(props){
    super(props);
       

    }
    render(){
        console.log(this.props)
        return(
        <li  className="list-group-item d-flex justify-content-between align-items-center">
            <div  className="form-check">
            <label  className="form-check-label">
            
                <input type="checkbox"  className="form-check-input" value=""/>
                {this.props.name}
                </label>
            </div>
        </li>
        )
    }
}
export default List;
